l adulto medio vive en permanente reflexión personal, replanteando sucesos vividos y reconsiderando la manera de relacionarse con sus parientes, en el entorno laboral, con sus círculos de amistad y demás personas cercanas.")])]),_c('div',{staticClass:"tarjeta bg-white p-4",attrs:{"x":"71.4%","y":"81%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Tardía")]),_c('p',[_vm._v("La adultez tardía puede estar asociada con cambios fisiológicos y hormonales que reflejan cambios profundos en lo físico y lo biológico; en esta adultez tardía, las hormonas sexuales bajan su producción y con ello se manifiestan cambios significativos en la fecundidad y la reproducción, Así mismo, la producción de algunas proteínas baja y empiezan a verse cambios más significativos en lo corporal. Los cambios cognitivos normales son: una mayor lentitud para pensar, una capacidad de aprendizaje más reducida y una disminución de la memoria de trabajo.")])])]),_vm._m(1),_vm._m(2),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-xl-8",attrs:{"data-aos":"fade-right"}},[_c('p',{staticClass:"mb-4"},[_vm._v("El desarrollo humano es un proceso complejo y multidimensional que ha sido explorado desde diversas perspectivas psicológicas. A través del PDF Autores, lo invitamos a adentrarse en las teorías de destacados autores que han marcado hitos en la comprensión de este fenómeno. Desde los fundamentos del Psicoanálisis de Sigmund Freud, que destaca las fuerzas inconscientes y las etapas psicosexuales, hasta las aportaciones psicosociales de Erik Erikson, que integran aspectos culturales y sociales en el desarrollo de la personalidad. Además, se analizan las transiciones de la vida adulta según Daniel J. Levinson y las etapas del desarrollo cognitivo propuestas por Warner Schaie, enfocadas en el uso del intelecto en contextos sociales. Este contenido ofrece una visión integral y enriquecedora que será clave para entender las dinámicas del crecimiento humano desde la niñez hasta