 rango de esta adultez oscila entre los 40 y 65 años, etapa que contiene varios hitos y cambios. En el caso de las características físicas, empiezan a presentarse signos de envejecimiento y la necesidad de ayudas visuales y auditivas. Por otro lado, la textura, grosor y regeneración de la piel, demuestran signos de la edad con manchas, primeras arrugas y despigmentación en el cabello (canas); en hombres y algunas mujeres, se presentan síntomas de alopecia.")]),_c('div',{staticClass:"d-flex align-items-start bg-morado-claro p-4 mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{staticClass:"me-3",staticStyle:{"width":"90px"},attrs:{"src":require("@/assets/curso/unidad/img-15.svg"),"alt":"Texto que describa la imagen"}}),_c('div',{staticClass:"d-flex flex-column"},[_c('p',{staticClass:"pe-xl-3 mb-0",attrs:{"data-aos":"fade-up"}},[_vm._v(" A medida que pasan los años y, de acuerdo con el estilo de vida, se evidencian reducciones en ciertas capacidades como la atención, orientación y memoria. De manera alternativa, algunos autores mencionan el término "),_c('strong',[_vm._v("inteligencia cristalizada")]),_vm._v(", que es la capacidad de adquisición de conocimientos a lo largo de la vida y de experticia a la hora de aplicarlos. Es un sinónimo de experiencia y sabiduría, a partir de los 40 años, siempre y cuando no haya alteraciones neurológicas.")])])])]),_c('div',{staticClass:"col-4 d-none d-xl-block",attrs:{"data-aos":"fade-left"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-16.png"),"alt":"Texto que describa la imagen"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-4 d-none d-xl-block",attrs:{"data-aos":"fade-right"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-17.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-12 col-xl-8",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"bg-amarillo p-4 mb-4"},[_c('p',